<template>
  <view class="my_header_box">
    {{ top }}
  </view>
</template>

<script>
  export default {
    data() {
      return {
        top: 0
      }
    },
    
    onLoad () {
      this.getSafeArea()
    },
    
    methods: {
      getSafeArea () {
        uni.getSystemInfo({
          success (res) {
            if (res.safeArea) {
              this.top = res.safeArea.top
            }
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
.my_header_box {
  background-color: #40c37f;
  padding-top: 47px;
}
</style>
